<template>
  <div>
    <div class="tab">
          <button @click="tab = 1">Search by name</button>
          <button @click="tab = 2">Search by serial number</button>
    </div>
    <div class="tabcontent" v-if="tab==1">
      <input v-model="name"/>
      <button @click="searchByName">Search by name</button>
    </div>
    <div class="tabcontent" v-if="tab==2">
      <input v-model="serialNumber"/>
      <button @click="searchBySerialNumber">Search by serial number</button>
    </div>
    <table v-if="equipList.length > 0">
      <tr>
        <th>Name</th>
        <th>Serial number</th>
        <th>Location</th>
      </tr>
      <tr v-for="equip, index in equipList" :equip="equip">
        <td>{{equip.name}}</td>
        <td>{{equip.serialNumber}}</td>
        <td>{{equip.location.name}}</td>
      </tr>
    </table>
  </div>
</template>
<script>
import axios from "axios"
const API_URL = 'http://localhost:8088/api/equip';
  export default{
    name: 'SearchEquip',
    data() {
      return {
        tab:1,
        name:"",
        serialNumber:"",
        equipList:[],
        equip:{}
      }
    },
    methods: {
      searchByName() {
        axios
          .get(API_URL + "/searchByName/" + this.name)
          .then(response => {
            this.equipList = response.data;
          });
      },
      searchBySerialNumber() {
        axios
          .get(API_URL + "/searchBySerialNumber/" + this.serialNumber)
          .then(response => {
            this.equipList = response.data;
          });
      }
    },
    mounted() {

    }
  }
</script>
<style>
/* Style the tab */
.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

/* Style the buttons that are used to open the tab content */
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}
</style>



