<template>
  <div>
    <h2 v-model='status'>{{status}}</h2>
    <table>
      <tr>
        <th>Name</td>
        <th>Serial number</th>
        <th>Location</th>
      </tr>
      <tr>
        <td><input v-model='currentEquip.name'></input></td>
        <td><input v-model='currentEquip.serialNumber'></input></td>
        <td>
          <select v-model='currentEquip.location'>
            <option v-for='location in locations' v-bind:value='location'>{{location.name}}</option>
          </select>
        </td>
        <td>
          <button @click="createEquip">Create equip</button>
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
import axios from 'axios';

const API_URL = 'http://localhost:8088/api/equip';

export default {
  name: 'CreateEquip',
  data() {
    return {
      currentEquip: {
        name: '',
        serialNumber:'',
        location: {}
      },
      locations: [],
      status: ' '
    }
  },
  methods: {
    createEquip() {
      axios
        .post(API_URL + '/createEquip', {currentEquip})
        .then(response => {
          this.status = 'Equip ' + response.data.name + ' was created successfully';
        });
    }
  },
  mounted() {
    axios.get(API_URL + '/loadLocations')
      .then(response => {
        this.locations = response.data;
      });
  }
}
</script>



